* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  background: url("../images/bg_image.jpg");
  background-size: cover;
  background-position: top;
  background-color: $color;
  font-family: "Open Sans";

  .container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 13% 37% 20% 20% 10%;
    grid-template-areas:
      "header"
      "song"
      "buttons"
      "progres"
      "footer";
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;

    background: linear-gradient(
      to top,
      rgba(33, 28, 54, 1),
      rgba(33, 28, 54, 1),
      rgba(33, 28, 54, 0.8)
    );
    z-index: -1;
  }
}

a {
  text-decoration: none;
}
img {
  width: 100%;
}

.header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0.5rem;
  .album {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-family: "Open Sans";

    span:first-child {
      font-size: 2rem;
      color: lighten($color, 30);
      opacity: 0.9;
    }

    span:nth-of-type(2) {
      font-size: 2.5rem;
      color: white;
    }
  }
}

.back {
  width: 1.5rem;
  height: 1.5rem;
  z-index: 200;
  margin-top: 2rem;
  margin-left: 4rem;
}

.header__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 3rem;
  height: 3rem;
  margin-top: 2rem;
  margin-right: 4rem;
  z-index: 150;
  cursor: pointer;
  transition: 2s;

  div {
    width: 5px;
    height: 5px;
    border-radius: 50px;
    background: white;
  }
}

// Song

.song {
  grid-area: song;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Open Sans";

  .song__image {
    width: 10rem;
    height: 10rem;
    margin: 0 2rem;
    border-radius: 50px;
    background: url("../images/unreleased_cover.png");
    background-size: cover;
    background-position: center;
    z-index: 50;
  }

  .song__title {
    font-size: 3rem;
    color: white;
    margin-top: 0.8rem;
    font-weight: 800;
  }

  .song__author {
    font-size: 2rem;
    color: lighten($color, 40);
    opacity: 0.5;
  }
}

// Buttons

.buttons {
  grid-area: buttons;
  display: flex;
  justify-content: space-around;
  align-items: center;

  z-index: 50;

  .play {
    width: 12rem;
    height: 12rem;

    background: url("../images/Play_inactive.png");
    background-size: cover;
    background-position: center;
    z-index: 50;
    cursor: pointer;
  }

  .play-active {
    width: 20rem;
    height: 20rem;
    background: url("../images/Play_active.png");
    background-size: cover;
    background-position: center;
    z-index: 50;
    cursor: pointer;
    img {
      width: 100%;
      height: 100%;
      z-index: 200;
    }
  }

  div {
    width: 3rem;
    height: 3rem;
  }

  .shuffle,
  .previous,
  .next,
  .loop {
    cursor: pointer;
  }
}

// Footer

.footer {
  grid-area: footer;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  bottom: 0;
  z-index: 150;

  &-menu {
    width: 5rem;
    height: 5rem;
    padding: 0.5rem;
    cursor: pointer;
  }

  &-text {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    flex-grow: 0.5;
  }
}

// Progress

.progres {
  grid-area: progres;
  height: 140px;
  display: flex;

  justify-content: center;
  align-items: center;
  transform: rotate3d(0, 1, 0, 180deg);

  &-lines {
    display: flex;
    flex-direction: column;
    margin: 0 1rem;
  }

  .line {
    width: 60vw;
    height: 1px;
    background: turquoise;
    z-index: 100;
  }
  .progres-line {
    position: absolute;
    width: 60vw;
    height: 5px;
    border-radius: 5px;
    background: turquoise;
    z-index: 100;
    align-self: flex-start;
    right: 345px;
    bottom: 67px;

    box-shadow: 0px 0px 6px aqua;
  }

  &-time-left {
    transform: rotate3d(0, 1, 0, 180deg);
    color: white;
    font-size: 1.5rem;
  }
}

// Menu

.menu {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: -100vw;
  background: rgba(33, 28, 54, 0.9);
  z-index: 100;
  transition: all 650ms cubic-bezier(1, 0, 0, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .album-cover {
    width: 10rem;
    height: 10rem;
    border-radius: 50px;
    background: url("../images/unreleased_cover.png");
    background-size: cover;
    background-position: center;
  }

  &-song-title {
    font-size: 3rem;
    color: white;
    margin-top: 0.8rem;
    font-weight: 800;
  }

  &-author {
    font-size: 1rem;
    color: lighten($color, 40);
    opacity: 0.5;
    font-family: "Open Sans";
  }

  .menu-dots,
  .add-to-playlist,
  .show-album,
  .share {
    margin: 1rem 0;
  }

  &-dots {
    display: flex;

    justify-content: space-between;
    width: 3rem;
    height: 3rem;

    div {
      width: 5px;
      height: 5px;
      border-radius: 50px;
      background: white;
    }
  }

  a {
    color: white;
    font-size: 2rem;
    font-family: "Open Sans";
    font-weight: 400;
  }
}

.open {
  right: 0;
}

// Playlist

.playlist {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: -100%;
  right: 0;
  background-color: white;
  z-index: 100;
  transition: all 650ms cubic-bezier(1, 0, 0, 1);

  .hide-menu {
    width: 5rem;
    height: 5rem;
    margin-bottom: 6rem;
    cursor: pointer;
  }

  ol {
    width: 70vw;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: space-between;

    li {
      display: flex;
      justify-content: space-between;
      align-items: space-between;
      font-size: 2rem;
      color: $color;
      margin: 1rem 0;
      cursor: pointer;
      z-index: 50;

      span {
        z-index: 1;
      }
    }
  }
  .btn-shuffle {
    width: 30rem;
    height: 5rem;
    border: none;
    background: $buttonColor;
    border-radius: 40px;
    color: white;
    font-size: 2rem;
    font-weight: 800;

    cursor: pointer;
  }
}

.playlist-open {
  bottom: -12%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  .hide-menu {
    width: 5rem;
    height: 5rem;
    margin-bottom: 10rem;

    cursor: pointer;
  }
  ol {
    width: 70vw;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: space-between;

    li {
      display: flex;
      justify-content: space-between;
      align-items: space-between;
      font-size: 2rem;
      color: $color;
      margin: 1rem 0;
      cursor: pointer;
    }
  }

  .btn-shuffle {
    width: 30rem;
    height: 5rem;
    border: none;
    background: $buttonColor;
    border-radius: 40px;
    color: white;
    font-size: 2rem;
    font-weight: 800;
    margin-bottom: 3rem;
    cursor: pointer;
  }

  .pagin-buttons {
    height: 100%;
    width: 10rem;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    .pagin-up,
    .pagin-down {
      width: 2.5rem;
      height: 2.5rem;
      cursor: pointer;
    }

    .pagin-up {
      transform: rotate(180deg);
    }
  }
}

// RWD

@media screen and (max-width: 425px) {
  .header {
    margin: 0;

    .back {
      width: 0.8rem;
      height: 0.8rem;
    }

    .album {
      span:first-child {
        font-size: 1rem;
      }

      span:nth-of-type(2) {
        font-size: 1.5rem;
        color: white;
      }
    }

    .header__menu {
      width: 1rem;
      height: 1rem;

      div {
        width: 3px;
        height: 3px;
        border-radius: 50px;
        background: white;
      }
    }
  }

  .song {
    .song__image {
      width: 10rem;
      height: 10rem;
    }

    .song__title {
      font-size: 2rem;
    }

    .song__author {
      font-size: 1rem;
    }
  }

  .buttons {
    .play {
      width: 6rem;
      height: 6rem;
    }

    .play-active {
      width: 10rem;
      height: 10rem;
    }

    div {
      width: 1.5rem;
      height: 1.5rem;
    }
  }

  .footer {
    &-menu {
      width: 3rem;
      height: 3rem;
      padding: 0.5rem;
    }
  }

  .menu {
    .album-cover {
      width: 7rem;
      height: 7rem;
    }

    &-song-title {
      font-size: 2rem;
    }

    &-author {
      font-size: 0.8rem;
    }

    .menu-dots,
    .add-to-playlist,
    .show-album,
    .share {
      margin: 1rem 0;

      a {
        font-size: 1rem;
      }
    }

    &-dots {
      width: 2rem;
      height: 2rem;

      div {
        width: 5px;
        height: 5px;
        border-radius: 50px;
        background: white;
      }
    }
  }
  .playlist {
    bottom: -200%;

    ol {
      li {
        font-size: 1rem;
        margin: 0.5rem 0;
      }
    }

    .btn-shuffle {
      width: 15rem;
      height: 2rem;
      margin-bottom: 0.5rem;
      font-size: 1rem;
    }
  }

  .playlist-open {
    bottom: 0%;

    .hide-menu {
      width: 3rem;
      height: 3rem;
    }
    ol {
      li {
        font-size: 1rem;
        margin: 0.5rem 0;
      }
    }

    .btn-shuffle {
      width: 15rem;
      height: 2rem;
      margin-bottom: 0.5rem;
      font-size: 1rem;
    }
  }
}
